@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,700;1,300&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #1a1a1c;
  color: #FFF;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text{
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border:none;
  background-color: #323235;
  color: #e2e2e2;
}
.coin-input::-webkit-input-placeholder{
  color: e2e2e2;
}
.coin-input:-ms-input-placeholder{
  color: e2e2e2;
}
.coin-input::placeholder{
  color: e2e2e2;
}

.coin-ul{
  display: flex;
  list-style: none;
}

.coin-ul li {
  margin: 0 60px;
  margin-top: 100px;
}
.coin-container{
    margin-top: 0;
    display: flex;
    justify-content: center;   
    
}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    min-height: 80px;
    border-bottom: 1px solid #d7d7d7;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}
.coin h1{
    font-size: 16px;
    width: 150px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;

}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price {
    width: 110px;
}

.coin-volume {
    width: 200px;

    
}

.coin-marketcap {
    width: 240px;
}

.coin-percent {
    width: 80px;
}

.red {
    color: red;
}

.green {
    color: green;
}


